import React, { useState } from "react";
import StForm from "./Stform";
import Admissionedit from "./Admissionedit";
export default function AdmissionIndex() {
  const [formreg, setformreg] = useState(1);
  const [formdata, setformdata] = useState();

  function getstdata(exam, st) {
    setformdata(exam);
  }
  function formstate(st, reg) {
    setformreg(st);
  }

  switch (formreg) {
    case 1:
      return <Admissionedit data={getstdata} />;
    case 2:
      return <StForm data={formdata} formstate={formstate} />;
    default:
  }
}
